.topbar {
    height: 50px;
    width: 100%;
    background-color: #1877f2;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    right: 0;
    left: 0;
    z-index: 999;

    &__wrapper {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 10px;
        gap: 20px;
        flex-wrap: wrap;
    }
    &__left {
        flex: 3;

        .logo {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
            cursor: pointer;
        }
    }

    &__center {
        flex: 5;

        .searchbar {
            width: 100%;
            height: 30px;
            background-color: #fff;
            border-radius: 30px;
            display: flex;
            align-items: center;

            &__icon {
                font-size: 20px !important;
                margin-left: 20px;
            }

            &__input {
                border: none;
                min-width: 230px;
                margin: 10px;

                &:focus {
                    outline: none;
                }
            }
        }
    }

    &__right {
        flex: 4;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #fff;

        &__link {
            margin-right: 10px;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 1px;
            cursor: pointer;
            color: #fff;
            text-decoration: none;
        }

        &__icons {
            display: flex;
            column-gap: 20px;

            &-item {
                position: relative;
                cursor: pointer;

                &--badge {
                    width: 15px;
                    height: 15px;
                    background-color: #bc1111;
                    border-radius: 50%;
                    color: #fff;
                    position: absolute;
                    top: -5px;
                    right: -5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 12px;
                }
            }

            &-img {
                width: 32px;
                height: 32px;
                border-radius: 50%;
                object-fit: cover;
                cursor: pointer;
                margin: 0 20px;
            }
            &-exit {
                cursor: pointer;
                color: #fff;
                text-decoration: none;
            }
        }
    }
}

@media (max-width: 770px){
    .topbar__wrapper {
        gap: 10px;
    }
    .topbar__right__icons{
        gap: 5px;
    }
}
@media (max-width: 710px){
    .topbar{
        height: 100px;
    }
    .topbar__right {
        justify-content: right;
    }
    .topbar__right__icons{
        gap: 10px;
    }
}
@media (max-width: 480px){
.topbar__wrapper{
    flex-direction: column;
    align-items: center;
}
.topbar{
        height: 150px;
    }

}