.sidebar {
    width: 15%;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    padding: 20px;
    position: sticky;
    top: 50px;


    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        &-item {
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            column-gap: 15px;
            cursor: pointer;
        }
    }

    &__btn {
        width: 150px;
        border: none;
        padding: 10px;
        border-radius: 5px;
        font-weight: 500;
        cursor: pointer;
    }

    &__hr {
        margin: 20px 0;
    }

    &__friendlist {
        padding: 0;
        margin: 0;
        list-style: none;
    }

}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background-color: #a8a6a6;
}